import { Injectable } from '@angular/core';
import {Platform, ToastController, App, NavController, Tabs, Alert, AlertController,IonicApp,Keyboard} from 'ionic-angular';
import { AppMinimize } from '@ionic-native/app-minimize';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
@Injectable()
export class BackbuttonService{

    //false
    backButtonPressed: boolean = false;
    alert:Alert

    // 
    constructor(public platform: Platform,
                public appCtrl: App,
                public toastCtrl: ToastController,
                private appMinimize:AppMinimize,
                private keyboard:Keyboard,
                private alertCtr:AlertController,
                private statusbar:StatusBar,
                private splashscreen:SplashScreen
                // private ionicApp:IonicApp
                ) {

                platform.ready().then(()=>{
                    this.statusbar.styleDefault();
                    this.splashscreen.hide();
                }).catch()

    }

    //registerBackButtonAction
    registerBackButtonAction(tabRef: Tabs): void {
        this.platform.registerBackButtonAction(() => {
            localStorage.getItem("flagBack")
            //NavController
            debugger;
            if(this.keyboard.isOpen()){
                this.keyboard.close();
                return;
            }

            let activeNav: NavController = this.appCtrl.getActiveNav();
            console.log(this.appCtrl);
            console.log(activeNav)

            //pop
            if(localStorage.getItem("flagBack")=="1"){
                if (tabRef == null || tabRef._selectHistory[tabRef._selectHistory.length - 1] === tabRef.getByIndex(0).id) {
                    //
                    this.showExit();
                } else {
                    //
                    // this.alert.dismiss();
                    tabRef.select(0);
                }
            }else{
                if (activeNav.canGoBack()) {
                    activeNav.pop();
                } else {
                    if (tabRef == null || tabRef._selectHistory[tabRef._selectHistory.length - 1] === tabRef.getByIndex(0).id) {
                        //
                        this.showExit();
                    } else {
                        //
                        // this.alert.dismiss();
                        tabRef.select(0);
                    }
                }
            }

        });
    }
    private alertT(content){

        this.alert = this.alertCtr.create({
            // spinner: 'hide',
            title:''  ,
            enableBackdropDismiss:false,
            // duration: 3000
        });
        this.alert.present();
    }
    //
    private showExit(): void {
        //true
        if (this.backButtonPressed) {
            // this.platform.exitApp();
                this.appMinimize.minimize();

        } else {
            //Toast
            this.toastCtrl.create({
                message: '',
                duration: 2000,
                position: 'bottom'
            }).present();
            //true
            this.backButtonPressed = true;
            //false
            setTimeout(() => this.backButtonPressed = false, 2000);
        }
    }
}



